import { Component, OnInit } from "@angular/core";
import { DataService } from "src/app/services/data.service";

@Component({
    selector: ".game-information__life-counter",
    templateUrl: "./life-count.component.html",
    styleUrls: ["./life-count.component.scss"]
})
export class LifeCountComponent implements OnInit{

    lifeCount: Number;

    // get and set shared properties from DataService
    constructor(private data: DataService){}

    ngOnInit(): void {
        this.data.currentLife.subscribe(lifeCount => this.lifeCount = lifeCount);
    }

}
