import { Component, OnInit } from "@angular/core";
import { SettingsService, RenderService } from "src/app/services/services";

@Component({
    selector: "game-rocket",
    template: "",
    styles: [""]
})
export class RocketComponent implements OnInit {

    // player position and size
    x = 0;
    y = 0;

    getBoundaries(){
        return {
            top: this.y,
            right: this.x + this.renderData.ROCKET_SIZE.width,
            bottom: this.y + this.renderData.ROCKET_SIZE.height,
            left: this.x,
        }
    }

    // player speed
    velocity = this.settings.SETTINGS.control.rocketSpeed;
    dx = 0;

    // shared properties
    ball = {
        isOnPlayer: true
    };

    // move player if it is not outside the game
    move() {
        if (((this.x + this.dx) > 0) && ((this.x + this.dx) < (this.renderData.CANVAS_SIZE.width - this.renderData.ROCKET_SIZE.width))) {
            this.x += this.dx;
        }
    }

    // stop player
    stop() {
        this.dx = 0;
    }

    setStartPosition(){
        this.x = (this.renderData.CANVAS_SIZE.width - this.renderData.ROCKET_SIZE.width) / 2;
        this.y = this.renderData.CANVAS_SIZE.height - this.renderData.ROCKET_SIZE.height;
    }

    ngOnInit(): void {
        throw new Error("Method not implemented.");
    }

    constructor(private settings: SettingsService, private renderData: RenderService) {
    }
}
