import { ScoreService } from '../../services/services';
import { Component, OnInit } from "@angular/core";

@Component({
    selector: ".game-information__game-score",
    templateUrl: "./score-count.component.html",
    styleUrls: ["./score-count.component.scss"]
})
export class ScoreCountComponent implements OnInit{

    // get and set shared properties from DataService
    constructor(private score: ScoreService){}

    currentScore: number;

    ngOnInit(): void {
        this.score.currentScore.subscribe(score => this.currentScore = score);
    }

}
