import { Injectable } from "@angular/core";

@Injectable({
    providedIn: 'root'
})
export class RenderService{
    WINDOW_SIZE = window.innerWidth;

    private canvasWidth = this.WINDOW_SIZE * 0.85;

    CANVAS_SIZE = {
        width: this.canvasWidth,
        height: this.canvasWidth / 2
    }

    BRICK_GRID = {
        columns: 20,
        rows: 20
    }

    private brickWidth = this.canvasWidth / this.BRICK_GRID.columns;

    BALL_SIZE = {
        diameter: this.brickWidth / 3
    }

    ROCKET_SIZE = {
        width: this.brickWidth * 2,
        height: this.BALL_SIZE.diameter
    }

    BRICK_SIZE = {
        width: this.brickWidth,
        height: this.brickWidth / 3
    }
}
