import { RenderService } from './../../services/render.service';
import { DataService } from 'src/app/services/data.service';
import { OnInit, Component } from '@angular/core';

@Component({
    selector: "game-brick",
    template: "",
    styles: [""]
})
export class BrickComponent implements OnInit {

    constructor(private renderData: RenderService ) {
        this.BRICK_SPRITE.src = "../../../assets/sprites/brick.png";
    }

    private x = 0;
    private y = 0;
    private isAlive: boolean;

    BRICK_SPRITE = new Image(this.renderData.BRICK_SIZE.width, this.renderData.BRICK_SIZE.height);

    setCoordinates(x: number, y: number){
        this.x = x;
        this.y = y;
    }

    setIsAlive(value: boolean){
        this.isAlive = value;
    }

    getIsAlive(){
        return this.isAlive;
    }

    getBoundaries(){
        return {
            top: this.y,
            right: this.x + this.renderData.BRICK_SIZE.width,
            bottom: this.y + this.renderData.BRICK_SIZE.height,
            left: this.x,
        }
    }

    ngOnInit(): void {

    }


}
