import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';

@Injectable({
    providedIn: 'root'
})
export class DataService {

    private life = new BehaviorSubject(2);
    currentLife = this.life.asObservable();

    isGameActive = false;

    changeLife(life: number) {
        this.life.next(life);
    }

    getCurrentLife() {
        return this.life.getValue();
    }

    resetLife() {
        this.changeLife(2);
    }

    constructor() {
    }
}
